.projects {
  padding: 80px 20px;
  background-color: #f4f4f4;
}

.projects h2 {
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.projects-grid {
  display: flex;
  gap: 20px;
  justify-content: center;
}

.project {
  flex: 1;
  padding: 20px;
  background-color: #fff;
  border-radius: 10px;
  text-align: center;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.project h3 {
  font-size: 1.5rem;
}

.project p {
  font-size: 1.1rem;
  color: #555;
}
