.skills {
  padding: 80px 20px;
  background-color: #fff;
}

.skills h2 {
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.skills-grid {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.skill {
  flex: 1;
  padding: 20px;
  background-color: #f8f9fa;
  border-radius: 10px;
  text-align: center;
}

.skill h3 {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.skill p {
  font-size: 1.1rem;
  color: #333;
}
